import React, { Component } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import panelJSON from "./score-card-json";
import { ScoreCard, OptionCard } from "./score-card";
import { Widget, XyChart } from "../../../model/domain/dashboard";
import { title } from "process";

const reorder = (list : any[], startIndex : number, endIndex: number) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

export default class DraggableArea extends Component<any, any> {
  constructor(props : any) {
    super(props);
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result : any) {
    if (!result.destination) {
      return;
    }

    const items = reorder(
      this.props.items,
      result.source.index,
      result.destination.index
    );
    this.props.setItems(items)
  }

  render() {
    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable direction="horizontal" droppableId="droppable">
          {(provided : any, snapshot : any) => (
            <div
            className="grid mobile-panel"
            style={{gridTemplateColumns: "repeat(5, 1fr)", width: '100%'}}
              {...provided.droppableProps}
              ref={provided.innerRef}
            >
              {this.props.items.map((item : Widget, index : number) =>{ 
                      if(!item) return null;
                  const { lineChart } = item;
                  const { backgroundColor } = lineChart as XyChart;
                     
                return(
                <Draggable key={JSON.stringify(item)} draggableId={JSON.stringify(item)} index={index}>
                  {(provided : any, snapshot : any) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}    
                    >
                      <ScoreCard 
                        name={title} 
                        value={10} 
                        bg={backgroundColor} editMode={this.props.editMode} remove={this.props.remove} index={index} />
                    </div>
                  )}
                </Draggable>
              )})}
              {provided.placeholder}
              {this.props.children}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
}

    