import React, { useState, useContext } from 'react'
import styled from 'styled-components';
import { Grid, Button, Text } from '../../../style/components';
import { primaryColor } from '../../../config';
import { OptionCard } from '../score-card/score-card';
import { Widget, ScoreCard } from '../../../model/domain/dashboard';

interface ScoreCardEditor {
  currentScoreCard: Widget[];
  setCurrent: any;
  options: Widget[];
  close: any;
};

const AddScoreCard = ({ currentScoreCard, setCurrent, options, close }: ScoreCardEditor) => {
  const NUM_CARDS = 5;
  const [cards, setCards] = useState<Widget[]>([]);
  const remainingCards = NUM_CARDS - currentScoreCard.length - cards.length;
  const select = (value: Widget) => setCards([...cards, value]);

  const unselect = (value: Widget) => setCards(cards.filter((el:  Widget) => el !== value))

  const confirm = () => {
    setCurrent([...currentScoreCard, ...cards]);
    close();
  }

  return (<div>
    <Text bold color={primaryColor} textAlign={"center"}>Select {remainingCards} more Score Cards</Text>
    <Grid cols={5}>
      {options
        .filter((item: Widget) => !currentScoreCard.includes(item))
        .map((item: Widget) => {
          const { title, scoreCard } = item;
          const { icon, description, textColor, backgroundColor } = scoreCard as ScoreCard
          return (
            <OptionCard
              onClick={() => cards.includes(item) ? unselect(item) : select(item)}
              title={title}
              key={title}
              el={<Number className="grid-box bold" >10</Number>}
              value={10}
              color={textColor}
              bg={backgroundColor}
              selected={cards.includes(item)}
            />
          );
        })}
    </Grid>
    <Button shadow style={{ fontSize: ".76em" }} onClick={() => confirm()}>Confirm</Button>
    <Button shadow style={{ fontSize: ".76em" }} onClick={() => {
      setCards(currentScoreCard)
      close()
    }}>Cancel</Button>
  </div>)
}


const Number = styled.div`
    font-size: 60px;
    color: white;`;

export default AddScoreCard;