import { useState, useEffect } from "react";
import API from "../api";
import { TimeSeriesQuery } from "../model/domain/metrics";
import moment, { Moment } from "moment";
import { previousDay, previousWeek, nextDay } from "../utils/date";

const interval = {
  startTime: previousWeek().toISOString(),
  endTime: new Date().toISOString(),
};
export const useMetricData = (props: TimeSeriesQuery) => {
  const [state, setState] = useState<TimeSeriesQuery>({...props, interval});
  const {
    interval: { startTime, endTime },
  } = state;
  
  const [data, setData] = useState({
    error: false,
    loading: false,
    data: [[]] as any,
  });

  const setEndTime = (newEndtime: Moment) => {
    setState({
      ...state,
      interval: getInterval(startTime, new Date(newEndtime.toDate()).toISOString())
    });
  };

  const setStartTime = (newStartTime: Moment) => {
    setState({
      ...state,
      interval: getInterval(new Date(newStartTime.toDate()).toISOString(), endTime),
    });
  };

  const getInterval = (startTime: string, endTime: string) => {
    if (isSmaller(startTime, endTime))
      return {
        startTime,
        endTime,
      };

    return {
      endTime: startTime,
      startTime: endTime,
    };
  };

  const isSmaller = (time1: string, time2: string) => {
    return moment(time1) < moment(time2);
  };

  const getMetricData = async (props: TimeSeriesQuery) => {
    try {
      const { interval: { startTime, endTime}} = props;
      props = {...props,  interval : { startTime , endTime: nextDay(new Date(endTime)).toISOString()}};

      setData({data : [[]], loading: true, error: false});
      const res  = await API.getMetricData(props);
      console.log(res)

      const data = res?.timeSeriesResult?.timeSeries?.map(({ points }: any)=>points) || [[]];
      setData({
        loading: false,
        error: false,
        data,
      });
    
    } catch (e) {
      setData({
        loading: false,
        error: true,
        data: [[]],
      });
    }
  };

 useEffect(() => {getMetricData({...state})}, [state.interval]);

  return {
    data,
    interval: state.interval,
    setEndTime,
    setStartTime,
  };
};

export default {
  useMetricData
};
