import React, { useEffect } from 'react';
import Layout from '../../layout';
import img from '../../assets/citations.svg' ;
import { Grid, Notification } from '../../style/components';

const Citations = ()=>{
    useEffect(()=>{
        Notification.warning({
            title: "Warning",
            message: "Page Under Construction"
        });
    }, [])
    return(<Layout sidebar>
        <Grid padding="6em 2em">

        <img src={img} style={{maxWidth: "80vw"}}/>
        </Grid>
    </Layout>);
}

export default Citations;