import React, { useContext, useEffect } from 'react';
import Sidebar from './style/components/sidebar';
import { NavContext } from './context/nav-context';
import styled from 'styled-components';
import { mobileWidth } from './config';
import TopNavBar from './style/components/topnav';

const Layout = ({ children, style, sidebar, background }: any) => {
    const { collapsed } = useContext(NavContext);
    useEffect(() =>
        window.scrollTo(0, 0), [])
    return (<>
        <div>
            {sidebar && <> <Sidebar />   <TopNavBar /></>}
        </div>
        <Wrapper background={background} style={style} sidebar={sidebar} collapsed={collapsed}>
            {children}
        </Wrapper>
    </>
    );
};

const Wrapper = styled.div<{ background: string | any, sidebar: any, collapsed: any }>`
${(props) => `
    padding-top: ${props.sidebar ? '4.5em' : 0};
    padding-left: ${ props.collapsed? "5em" : props.sidebar ? '10em' : 0};

    background: ${props.background ? `url(${props.background})` : '#F0F3F4'};`}
    margin: auto;
    min-height: 100vh;
    display: grid;
    place-items: start center;
    transition: all 0.5s ease-out;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position:  center;
    width: calc (11em - 100vw );
    background-size: cover;
    @media(max-width: ${mobileWidth}){ 
        padding-left: 0em; 
        padding-bottom: 5em; 
        width: 100vw;
    }`;
export default Layout;