import React, { useState, useEffect } from 'react';

const withStorage = (WrappedComponent : any) => {
    class HOC extends React.Component {
    
      loadData = (key : string) => localStorage?.getItem(key) || ""; 
      
      saveData = (key : string, data : string) => localStorage.setItem(key, data);
      
      removeData = (key : string) => localStorage.removeItem(key);
    
      render() {
        return (
          <WrappedComponent
            loadData={this.loadData}
            saveData={this.saveData}
            removeData={this.removeData}
            {...this.props}
          />
        );
      }
      
}
return HOC;
}

export interface Storable {
  loadData : (key : string ) => string;
  saveData : (key : string, data : string ) => void;
  removeData : (key : string ) => void;
}

export default withStorage;