import React, { useEffect, useState } from "react";
import { Grid, Button, Text } from "../../../style/components";
import img from "../../../assets/enterprise.svg";
import { Select as Sel } from "antd";
import { primaryColor } from "../../../config";
import { useEnterprises } from "../../../hooks/enterprises";
import { Enterprise } from "../../../model/domain/enterprise";
import store from "../../../store";
import { addEnterprise, logout } from "../../../store/actions/user";
import { useSelector, useDispatch } from "react-redux";


const Pending = () => {
  return (
    <Wrapper>
      <img
        src={img}
        style={{ height: "50%", maxWidth: "40vw", width: "300px" }}
      />
      <Text textAlign="left" bold>
        Wait till you get Approved...Blah Blah
      </Text>
    </Wrapper>
  );
};

const Select = () => {
  const userId = useSelector((state: any) => state.user.user.id);
  const loading = useSelector((state: any) => state.user.loading);
  const dispatch = useDispatch();
  const { enterprises, loading: loadingEnterpise, error } = useEnterprises() as any;

  const [ selected, setSelected ] = useState<any>("");
  return (  
    <Wrapper>
      <img
        src={enterprises[0]?.imageUrl || img}
        style={{  maxWidth: "40vw", width: "200px" }}
      />
      <Text textAlign="left" bold>
        {"Select your Organization"}
      </Text>
      <div style={{ width: "100%" }}>
        <Sel 
            placeholder="Select" 
            style={{ width: "95%" }} 
            value={selected}
            onChange={(e: any)=> setSelected(e)}
        >
          {enterprises?.map(({ id, title, imageUrl }: Enterprise) => (
            <Sel.Option key={id} value={id}>{title}</Sel.Option>
          ))}
        </Sel>
        <Grid cols="1" margin="2em 0">
          <Button
            loading={loading}
            disabled={loading}
            invert
            width="95%"
            onClick={() => dispatch(addEnterprise(selected, userId))}
          >
            Confirm
          </Button>
        </Grid>
      </div>
    </Wrapper>
  );
};

const Wrapper = ({ children }: any) => {
  const dispatch = useDispatch();
  return (
    <Grid
      customRows="auto 1fr auto"
      style={{ padding: "3em" }}
      className="shake"
    >
      <div
        className="bold pointer txt-right"
        onClick={()=>dispatch(logout())}
        style={{ color: primaryColor, width: "100%", padding: "1em 2em" }}
      >
        Logout
      </div>
      {children}
    </Grid>
  );
};

export default {
  Select,
  Pending,
};
