import React, { useState } from 'react';
import { Grid, Button, Text } from '../../style/components';
import { Input, DatePicker } from 'antd';
import { useFormik } from 'formik';
import moment, { Moment } from 'moment';
import * as Yup from 'yup';

const validationSchema = Yup.object({
    title: Yup.string().required("Required").min(1, ""),
    body: Yup.string().required("Required!"),
    launchAt: Yup.string().required("Required!").min(1, ""),
    expiresAt: Yup.string().required("Required!").min(1, ""),
});

const defaultValues = { title: "", body: "", status: "active", launchAt: new Date(), expiresAt: new Date() };

const Form = ({ add, close, initialValues = defaultValues }: any) => {
    const [loading, setLoading] = useState(false);
    const { handleSubmit, handleChange, handleBlur, errors, values, setFieldValue, isValid } = useFormik({
        initialValues,
        onSubmit(values) {
            submit(values)
        },
        validationSchema
    })
    const submit = async (values: any) => {
        try {
            setLoading(true)
            await add(values);
            close();
        } catch (e) {

        } finally {
            setLoading(false)
        }
    }

    const error = !values.expiresAt || !values.launchAt || !values.body || !values.title
    const style = {
        padding: ".5em 1em", height: "100%", width: "100%",
        placeItems: "center stretch", gridGap: ".4em", background: "white"
    }

    return (<form onSubmit={handleSubmit} className="grid" style={style}>
        <div>
            <Text textAlign="left" bold>Annoucement Title</Text>
            <Input
                name={"title"}
                value={values.title}
                onChange={handleChange}
                placeholder="Example: SJSU Graduation Ceramony"
            />
        </div>

        <div>
            <Text textAlign="left" bold>Annoucement Message</Text>
            <Input.TextArea  name={"body"} value={values.body}
                onChange={handleChange}
                placeholder="Example: Information on the Event" />
        </div>

        <div className="grid" style={{placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>Message Launch Date</Text>
            <DatePicker
                showTime
                minuteStep={15}
                value={moment(values.launchAt, "YYYY-MM-DD HH:mm:ss")}
                onChange={(e: Moment | null, date: string) => setFieldValue("launchAt", new Date(date))} />
        </div>

        <div className="grid" style={{placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>Message Expiry Date</Text>
            <DatePicker
                showTime
                minuteStep={15}
                value={moment(values.expiresAt, "YYYY-MM-DD HH:mm:ss")}
                onChange={(e: Moment | null, date: string) => setFieldValue("expiresAt", new Date(date))} />
        </div>

        <Grid cols="2" size="0.7em" placeItems={"stretch"}>
            <Button.Normal
                type="submit"
                disabled={loading}
                onClick={() => close()}
            >
                Cancel
                </Button.Normal>
            {loading ? <Button loading disabled>Loading</Button> :
                <Button.Normal
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
                </Button.Normal>
            }
        </Grid>
    </form>
    )
}

export default Form;
