import React, { useContext, useEffect } from 'react';
import { Grid, Button, Placeholder } from '../../style/components';
import { getComponents } from './util/chart-builder';
import GraphAdder from './modals/graph';
import { Widget } from '../../model/domain/dashboard';
import { gray } from '../../config';
import { useWidgets } from '../../hooks/widget';
import { useSelector } from 'react-redux';
import { ComponentsContext } from '../../context/components';

const Analytics = () => {
  const userId = useSelector((state: any) => state.user.user.id);
  const { widgets, getEnterprise, loading, addWidget, removeWidget } = useWidgets({ userId });
  const { editMode, toggleEditMode } = useContext(ComponentsContext);

  useEffect(() => {
    if (userId) getEnterprise(userId);
  }, [userId]);

  return (
    <div style={{ placeItems: "start center", background: gray, padding: "1em" }}>
      <Grid cols="2">
        <Button
          style={{ gridColumn: "1/-1", placeSelf: "end" }}
          rounded
          shadow
          onClick={toggleEditMode} minWidth="4.5em">
          <i className={`fa fa-${!editMode ? 'edit' : 'check'}`} ></i>
        </Button>
        {widgets?.map((item: Widget | any, i: number) => getComponents(item, () => removeWidget(i)))}
        {loading ? <Placeholder number={2} style={{ maxWidth: "600px", minHeight: "200px" }} /> :
          <GraphAdder add={(el: Widget) => {
            addWidget(el)
          }} />}
      </Grid>
    </div>
  );
}

export default Analytics;
