import React from 'react';
import { Input, Form } from 'antd';
import { Button, Text, Grid } from '../../../style/components';
import { useFormik } from 'formik';
import { login } from '../../../store/actions/user'
import * as  Yup from 'yup';
import { connect, useDispatch } from 'react-redux';
import store from '../../../store';

const loginFields = [
    {
        id: "email",
        label: "Your email",
        placeholder: "Example: John.Doe@parkstash.com",
        hide: false
    },
    {
        id: "authSecret",
        label: "Your Password",
        placeholder: "Example: ●●●●●●●",
        hide: true
    }]


const validationSchema = Yup.object({
    email: Yup.string().required("Required!").email(),
    authSecret: Yup.string().required("Required!").min(6, "Password must contain at least 6 characters")
});

type Values = 'authSecret' | 'email'


const Login = ({ passwordForgotToggler, loading }: any) => {
    const dispatch = useDispatch();
    const { handleSubmit, handleChange, values, errors, } = useFormik({
        initialValues: { email: "", authSecret: "", authType: "password" },
        onSubmit(values) {
           dispatch(login(values))
        },
        validationSchema
    })

    const error = loginFields.reduce((acc: boolean, { id }: any) => acc || Boolean(errors[id as Values]), false);
    return (
        <form
            onSubmit={handleSubmit}
            className="shake"
            style={{ display: "grid", paddingTop: "2em", width: "100%", placeItems: "center stretch", gridGap: ".4em" }}>
            {loginFields.map(({ id, placeholder, label, hide }: any) => (
                <div style={{ paddingTop: ".5em" }} key={id}>
                     <Text textAlign="left" bold>{label} <span className="red">*</span></Text>
                    <Input
                        disabled={loading}
                        type={hide ? 'password' : 'text'}
                        name={id}
                        value={values[id as Values]}
                        onChange={handleChange}
                        placeholder={placeholder} />
                </div>
            ))}

            {loading ? <Button loading disabled>Loading</Button> :
                <Button.Normal
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
                </Button.Normal>
            }

            <div><span className="pointer hover-bold" onClick={passwordForgotToggler}>Forgot Password?</span></div>
        </form>)
}


const mapStateToProps = (state: any) => {
    const { garages } = state
    return { garages }
};

export default connect(mapStateToProps, {})(Login);
