import { Badge } from '../../style/components/badge';
import { Space } from 'antd';
import { Button } from '../../style/components';
import React from 'react';
import { Popconfirm } from 'antd';


const columns = (edit: any, remove: any, loadingAction = false, index = 0) => {
    return [{
        title: 'Title',
        dataIndex: 'title',
        key: 'title',
        render: (text: string) => <a>{text}</a>,
    },
    {
        title: 'Message',
        dataIndex: 'body',
        key: 'body',
    },
    {
        title: 'Status',
        dataIndex: 'status',
        render: (text: string) => <Badge
            className="shadow txt-center"
            style={{ background: text === 'expired' ? "red" : "" }}
        >
            {text}
        </Badge>,
    },
    {
        title: 'Launch Date',
        key: 'launchAt',
        dataIndex: 'launchAt',
        render: (text: string) => new Date(text).toISOString().substring(0, 10)
    },
    {
        title: 'Expiry Date',
        key: 'expiresAt',
        dataIndex: 'expiresAt',
        render: (text: string) => new Date(text).toISOString().substring(0, 10)
    },
    {
        title: 'Action',
        key: 'action',
        render: (_: any, record: any) => {
            return (
                <Space size="middle">
                    <Button
                        shadow
                        disabled={loadingAction}
                        loading={loadingAction}
                        onClick={() => edit(record)}
                    >
                        <i className="fa fa-edit"></i> &nbsp;Edit
                    </Button>


                    <Popconfirm 
                        title="Are you sure"
                        onConfirm={() => remove(record)}
                    >
                        <Button
                            shadow
                            bg="red"
                            disabled={loadingAction}
                          
                            loading={loadingAction}
                        >
                            <i className="fa fa-trash"></i> &nbsp;Delete
                        </Button>
                     </Popconfirm>
                  
                </Space>
            )
        },
    },
    ]
};

export default columns;