import { useState, useEffect } from 'react';
import API from '../api'
import { Garage } from '../model/domain/garage';
import { asyncHandler } from "../utils";

export const useGarages = () => {
  const [state, setter] = useState({
    garages: [],
    enterprise: {
      id: "",
    },
    loading: false,
    error: false,
  });

  const setState = (obj: any) => setter({ ...state, ...obj });

  const getGarages = async (eId: string) => {
    setState({ loading: true, error: false });
    const { garages = [], success = false } = await asyncHandler({
      apiCall: () => API.getGarages(eId),
    });
    setState({ loading: false, error: !success, garages });
  };

  const addGarage = async (garage: Garage) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.addGarage(state.enterprise.id, garage),
      successMessage: "Garage was Added",
      errorMessage: "Garage was not Added",
    });
    setState({ loading: false, error: res?.success || false });
    getGarages(state.enterprise.id)
  };

  const editGarage = async (garage: Garage) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () =>API.editGarage(state.enterprise.id, garage),
      successMessage: "Garage was Edited",
      errorMessage: "Garage was not Edited",
    });
    setState({ loading: false, error: res?.success || false });
    getGarages(state.enterprise.id)
  };

  const getEnterprise = async (userId: string) => {
    setState({ loading: true, error: false });
    try {
      const res = await API.getEnterpriseByUserId(userId);
      setState({
        loading: false,
        error: res?.success || false,
        enterprise: res.enterprise || {},
      });
    } catch (e) {
      console.log(e)
    }
  };


  const refresh = () => getGarages(state.enterprise.id);

  useEffect(() => {
    if (state.enterprise && state.enterprise.id)
      getGarages(state.enterprise?.id);
  }, [state.enterprise]);

  return {
    ...state,
    addGarage,
    getEnterprise,
    editGarage,
    refresh
  };
};

export default useGarages;
