import React, { useState, useEffect } from "react";
import Layout from "../../layout";
import { Card, Grid, Switch, Header } from "../../style/components";
import { Table } from "antd";
import { primaryColor, mobileWidth } from "../../config";
import styled from "styled-components";
import { useSelector } from "react-redux";
import { useEnterprise } from "../../hooks/enterprise";
import columns from "./columns";
import CountUp from "react-countup";
import empty from "../../assets/empty.svg";

const states = {
  ACCEPTED: "Enabled",
  ALL: "ALL",
  DEFAULT: "ALL",
  PENDING: "Onboarding",
  states: ["Enabled", "ALL", "Onboarding"],
};

const Number = styled.div`
  font-size: 60px;
  color: white;
`;

let locale = {
  emptyText: (
    <span>
      <Grid>
      <img src={empty} style={{ maxWidth: "40vw", width: "200px", marginTop: "10vh" }} />
        <Header bold> No Users</Header>
      </Grid>
    </span>
  )
};

const ScoreCard = ({ name, value, bg }: any) => {
  return (
    <div style={{ minWidth: "20%" }}>
      <Card
        shadow
        hover
        className="noselect"
        color="white"
        style={{ background: bg, cursor: "pointer", backgroundSize: "cover" }}
      >
        <div
          className="txt-center bold"
          style={{ fontSize: ".9em", color: "white", fontWeight: "bold" }}
        >
          <span
            style={{ textShadow: "1px 1px gray", textTransform: "capitalize" }}
            dangerouslySetInnerHTML={{ __html: name + " " }}
          />
          <i className="fas fa-user right txt-shadow txt-md"></i>
        </div>
        <Number className="grid-box bold">
          <CountUp duration={2} start={0} end={value} />
        </Number>
      </Card>
    </div>
  );
};

const Controls = () => {
  const userId = useSelector((state: any) => state.user.user.id);

  const {
    getEnterprise,
    enterpriseUsers,
    loading,
    enterprise,
    acceptEnterpriseUser,
    removeEnterpriseUser,
    rejectEnterpriseUser,
  } = useEnterprise(userId);

  const [state, setState] = useState({
    active: states.DEFAULT,
    accepted: 0,
    total: 0,
  });

  const current = enterpriseUsers.filter(
    ({user: {
        apps: { enterpriseApp },
      },
    }: any) =>
      enterpriseApp === state.active.toLowerCase() ||
      state.active === states.ALL
  );

  const findAccepted = (users: any []) =>
    users.reduce((
        acc: number,
        {
          user: {
            apps: { enterpriseApp },
          },
        }: any
      ) => (acc + (enterpriseApp === "enabled" ? 1 : 0)), 0);

  useEffect(
    () =>
      setState({
        ...state,
        total: enterpriseUsers.length,
        accepted: findAccepted(enterpriseUsers),
      }),
    [enterpriseUsers]
  );

  useEffect(() => {
    console.log(enterpriseUsers)
  }, [enterpriseUsers]);

  useEffect(() => {
    if (userId) getEnterprise(userId);
  }, [userId]);

  return (
    <Layout sidebar>
      <Grid gridGap="0">
        <Grid cols={4} width="100%" placeItems="stretch">
          <ScoreCard
            name="Users Accepted"
            value={state.accepted}
            bg={"coral"}
          />
          <ScoreCard
            name="Users Pending"
            value={state.total - state.accepted}
            bg={"dodgerblue"}
          />
          <ScoreCard name="All Users" value={state.total} bg={primaryColor} />
        </Grid>
        <Switch
          active={state.active} 
          items={states.states}
          onClick={(active : string) => setState({ ...state, active })}
         />
        <Card shadow minWidth="60vw">
          <DIV>
            <Table
              locale={locale}
              rowKey={(record) => record.user.id}
              loading={loading}
              style={{ color: primaryColor, maxWidth: "90vw" }}
              dataSource={current}
              columns={columns(
                acceptEnterpriseUser,
                rejectEnterpriseUser,
                removeEnterpriseUser,
                loading
              )}
            />
          </DIV>
        </Card>
      </Grid>
    </Layout>
  );
};

const DIV = styled.div`
  @media (max-width: ${mobileWidth}) {
    overflow-x: scroll;
  }
`;

export default Controls;
