import React from 'react';
import { Input, Tooltip } from 'antd';
import { Button, Text, Grid } from '../../../style/components';
import { useFormik } from 'formik';
import { signup } from '../../../store/actions/user';
import * as  Yup from 'yup';
import { connect } from 'react-redux';

const signupFields = [
    {
        id: "name",
        label: "Your Name",
        placeholder: "Example: John Doe",
        hide: false
    },
    {
        id: "email",
        label: "Your email",
        placeholder: "Example: John Doe",
        hide: false
    },
    {
        id: "authSecret",
        label: "Your Password",
        placeholder: "Example: ●●●●●●●",
        hide: true
    },
    {
        id: "confirmPassword",
        label: "Confirm Password",
        placeholder: "Example: ●●●●●●●",
        hide: true
    }
]
type Values = 'name' | 'email' | 'authSecret' | 'confirmPassword'

const validationSchema = Yup.object({
    email: Yup.string().required("Required!").email("Must be a Valid Email"),
    authSecret: Yup.string().required("Required!").min(6, "Password must contain at least 6 characters"),
    confirmPassword: Yup.string()
        .required("Required!")
        .min(6, "Password must contain at least 6 characters")
        .oneOf([Yup.ref('authSecret')], 'Passwords must match'),
    name: Yup.string().required("Required!")
});
const Sigup = ({ loading, signup }: any) => {
    const { handleSubmit, handleChange, errors, values, touched, handleBlur } = useFormik({
        initialValues: { name: "", email: "", authSecret: "", confirmPassword: "", authType: "password" },
        validationSchema,
        onSubmit(values) {
            signup(values)
        }
    });

    const error = signupFields.reduce((acc: boolean, { id }: any) => acc || Boolean(errors[id as Values]), false);
    return (
        <form
            className="shake grid"
            onSubmit={handleSubmit}
            style={{background: "white", paddingTop: "1em", width: "100%", placeItems: "center stretch", gridGap: ".4em" }}>
            {signupFields.map(({ hide, id, placeholder, label }: any) => {
                const error = errors[id as Values]
                return (
                    <div style={{ paddingTop: ".5em" }} key={id}>
                        <Text textAlign="left" bold>{label} <span className="red"> *</span></Text>
                        <Input
                            type={hide ? 'password' : 'text'}
                            name={id}
                            onBlur={handleBlur}
                            suffix={
                                touched[id as Values] && <Tooltip title={error}>
                                    <i className={`fa fa-${error ? 'times-circle red' : 'check green'}`} style={{ position: "relative" }}></i>
                                </Tooltip>
                            }
                            value={values[id as Values]}
                            onChange={handleChange}
                            placeholder={placeholder} />
                    </div>)
            })}

            {loading ? <Button loading disabled>Loading</Button> :
                <Button.Normal
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
                </Button.Normal>
            }
        </form>)
}

function mapDispatchToProps(dispatch: (I: any) => (any)) {
    return {
        signup: (values: any) => dispatch(signup(values))
    };
}

export default connect(
    null,
    mapDispatchToProps
)(Sigup);