  
 const day =  24 * 60 * 60 * 1000;
export const nextWeek = ()=>{
    const today = new Date();
    return new Date(today.getTime() + 7 * day);;
}


export const nextDay = (d : Date)=>{
    return new Date(d.getTime() + day);;
}

export const previousDay = ()=>{
    const today = new Date();
    return new Date(today.getTime() + day);;
}

export const previousWeek = ()=>{
    const today = new Date();
    return new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);;
}


export const nextMonth = ()=>{
    const today = new Date();
    return new Date(today.getFullYear(), today.getMonth() + 1, today.getDate());
}

export const nextSeason = ()=>{
    const today = new Date();
    return new Date(today.getFullYear(), today.getMonth() + 6, today.getDate());
}
export const adjustDateTime = (date: any, time : any) => {
    time = new Date(time);
    time.setHours(time.getHours() - 5);
    date = new Date(date);
    date.setHours(time.getHours());
    date.setMinutes(time.getMinutes());
    time =  String(new Date(time.getTime())).substring(16, 21)
    return { date, time };
}
export const today = ()=>{
const thisDay = new Date()
return new Date(thisDay.getFullYear(), thisDay.getMonth(), thisDay.getDate(), thisDay.getHours(), thisDay.getMinutes())
}

export const to12Hours = (date: Date)=>{
 let options = {
  hour: 'numeric',
  minute: 'numeric',
  hour12: true
};
return date.toLocaleString('en-US', options);
}