import React, { useState, useRef } from 'react';
import { Grid, Button } from '../../../style/components';
import img from '../../../assets/login.svg'
import { GoogleLogin } from 'react-google-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';


const FacebookBtn = ({ login, className, ref }: any) => {
  const [user, setUser] = useState({});
  const response = (res: any) => {
  }

  let adapter = (profile: any) => {
    let { givenName: name, familyName: lname, email, imageUrl: photo, googleId } = profile;
    return { googleId, email, name };
  }
  return (
    <div className={className}>
      <FacebookLogin
        render={(renderProps: any) => <Button
          invert
          onClick={renderProps.onClick}>
          <i className="fab fa-facebook-square"></i>&nbsp;
        Signup With Facebook</Button>}
        appId="1088597931155576"
        fields="name,email,picture"
        onClick={response}
        callback={response} />
    </div>);
};

const GoogleBtn = ({ login, className }: any) => {
  const [user, setUser] = useState({});

  const response = (res: any) => {
  }

  let adapter = (profile: any) => {
    let { givenName: name, familyName: lname, email, imageUrl: photo, googleId } = profile;
    return { googleId, email, name };
  }

  return (
    <div className={className}>
      <GoogleLogin
        render={renderProps => <Button
          invert onClick={renderProps.onClick}>
          <i className="fab fa-google"></i>&nbsp;
         Signup With Google
     </Button>}
        clientId="432904370571-10sna4o5st6k2aee0q3p09bt05qjgbng.apps.googleusercontent.com"
        buttonText="Login with Google"
        onSuccess={response}
        onFailure={console.log}
        cookiePolicy={'single_host_origin'}
      /></div>);
};


const AuthBox = () => (
  <Grid
    height="100%"
    padding="3em"
    background="linear-gradient(to right top, #0bc062, #00b78b, #00aba7, #009cb4, #0d8cb0);">
    <img src={img} style={{ width: "500px", maxWidth: "40vw" }} />
    <Grid cols="2">
      <FacebookBtn />
      <GoogleBtn />
    </Grid>
  </Grid>
);

export default AuthBox;