
import Key from '../json/keys.json'

function header() {
    return { "Content-Type": "application/json" };
  }
  
  function authHeader() {
    let authtoken = localStorage.getItem(Key.USER) || "";
    return { "Content-Type": "application/json", authtoken };
  }
  
  
  function authUploadHeader() {
    let authtoken = localStorage.getItem(Key.USER) || "";
    return {  'content-type': 'multipart/form-data', token: authtoken };
  }
  
  export { header , authHeader, authUploadHeader };