import styled from 'styled-components';
import React from 'react';
import { sizeStyle, fontStyle, FontStyle, SizeStyle, ElementProps } from '../../generator';


interface HeaderProps extends FontStyle, SizeStyle, ElementProps{
    children? : React.ReactNode;
}

const HeaderLayout = styled.h1<{ styleProps: SizeStyle, fontProps: FontStyle }>`
${props => sizeStyle(props.styleProps)}
${props=> fontStyle(props.fontProps, false)}`;

export const Header = (props: HeaderProps) => {
    const { children, style, className, onClick } = props;

    const { width, height, max, padding, margin, minWidth } = props;
    const styleProps : SizeStyle = { 
        width, 
        height, 
        max, 
        padding, 
        margin, 
        minWidth 
    };

    const { size, color, bold, textAlign, placeSelf, textShadow } = props;
    const fontProps : FontStyle = {
        size: typeof size === 'number'? (6 - size)/2 + "rem": size? size : "1.5em",
        color, 
        bold, 
        textAlign,  
        placeSelf, 
        textShadow
    };

    return (
        <HeaderLayout styleProps={styleProps}  fontProps={fontProps} onClick={onClick} className={className} style={style}> {children} </HeaderLayout>
    );
}

