import React, { useEffect } from "react";
import { Garage } from "../../model/domain/garage";
import { Header, Grid, Button, Modal, Placeholder } from "../../style/components";
import useGarages from "../../hooks/garage";
import { useSelector } from "react-redux";
import empty from "../../assets/empty.svg";
import { gray } from "../../config";
import loader from "../../assets/loader.svg";
import GarageCard from "./garage-card";
import { GarageMultiStep } from "./garage-add";
import { GarageEditor } from "./garage-edit";

const Listings = () => {
  const userId = useSelector((state: any) => state.user.user.id);
  const { getEnterprise, garages, loading, refresh } = useGarages();

  const loader = <Placeholder number={5} style={{maxWidth: "600px", minWidth: "40vw"}}/>
  const noGarages = !garages || garages?.length === 0;
  useEffect(() => {
    if (userId) getEnterprise(userId);
  }, [userId]);

  const AddModal = Modal({
    title: "Add Your Garage",
    message: <GarageMultiStep close={()=>AddModal.close()} userId={userId} refresh={refresh}/>,
    hideCancel: true,
  });
  const edit = (garage: Garage)=>{
    const EditGarage = Modal({
      title: "Edit Your Garage",
      message: <GarageEditor close={()=>EditGarage.close()} userId={userId} refresh={refresh} garage={garage}/>,
      hideCancel: true,
    });

    EditGarage.open();
  }

  return (
    <Grid
    width="100%"
    background={gray}
      style={{
        minWidth: "60vw",
        minHeight: '90vh'
      }}
    >
      <Grid placeSelf="start stretch">
        {loading ?  loader : (
          <div className="grid">
            <Button
              style={{ placeSelf: "end"}}
              margin="1em"
              rounded
              shadow
              onClick={() => AddModal.open()}
              minWidth="4.5em"
            >
              <i className={`fa fa-${"plus"}`}></i> Add
            </Button>
            {noGarages ? (
              <div>
                <img src={empty} style={{ maxWidth: "40vw", width: "300px", marginTop: "10vh" }} />
                <Header bold size={2}>
                  No Garage Listed
                </Header>
              </div>
            ) : (
              garages.map((g: Garage) => <GarageCard {...g} edit={()=>edit(g)}/>)
            )}
          </div>
        )}
      </Grid>
    </Grid>
  );
};

export default Listings;
