import React, { Suspense, lazy, useEffect, useState } from 'react';
import { Garage } from './pages';
import { Route, Switch } from "react-router-dom";
import ErrorBoundary from 'antd/lib/alert/ErrorBoundary';
import Announcement from './pages/announcement';
import Citations from './pages/citations';
import Events from './pages/events';
import Profile from './pages/profile';
import Settings from './pages/settings';
import Login from './pages/login';
import { Loader } from './style/components';
import { useSelector, useDispatch } from 'react-redux'
import userStates from "./json/user-state.json";
import store from './store';
import { auth } from './store/actions/user';
import Controls from './pages/controls';

function App() {
  const userState = useSelector((state : any) => state.user.userState);
  const dispatch = useDispatch();
  const [ state, setState ] = useState({loading: true});

  useEffect(()=>{
      dispatch(auth());
      setTimeout(()=>setState({...state, loading: false}), 2000)
}, [])
  return (
   !state.loading? <ErrorBoundary>
      {userState === userStates.APPROVED? 
        <Switch> 
          <Route path="/announcements"  render={() => <Announcement />} />
          <Route path="/events" render={() => <Events />} />
          <Route path="/citations" render={() => <Citations />} />
          <Route path="/profile" render={() => <Profile />} />
          <Route path="/settings" render={() => <Settings />} />
          <Route path="/controls" render={() => <Controls />} />
          <Route path="/" render={() => <Garage />} />
        </Switch>: 
        <Route path="/" render={() => <Login/>} />}
    </ErrorBoundary>: <Loader />
  );
}

export default App;