import React from 'react';
import { Card , Grid } from '../../style/components';
import Layout from '../../layout';
import AuthBox from './user-select/auth';
import UserHandleBox from './user-handle';
import bg from '../../assets/login-bg.svg';

const Login = ()=>{
    return<Layout background={bg} >
        <Grid height="100%">   
            <Card   
                padding="0"
                grid 
                shadow
                placeItems={"stretch"}
                customCols={"3fr 2fr"}
            > 
            <AuthBox />
            <UserHandleBox/>
            </Card>
        </Grid>
    </Layout>
}

export default Login