import { Space } from 'antd';
import { Button, Badge } from '../../style/components';
import React from 'react';
import { User } from '../../model/domain/user';

const columns = (accept:  any, reject: any, remove: any, loadingAction = false) => {
    return [{
        title: 'Name',
        dataIndex: 'user',
        key: 'user',
        render: (user: User) => <a>{user.name}</a>,
    },
    {
        title: 'Email',
        dataIndex: 'user',
        key: 'user',
        render: (user: User) => <a>{user.email}</a>,
    },
    {
        title: 'Status',
        dataIndex: 'role',
        key: 'role',
        render: (_: string, record: any) => {
            const approved = record?.user?.apps?.enterpriseApp === 'enabled'

            return (
                <i className={`fas fa-${approved ? 'check' : 'times'}-circle ${!approved && 'red'}`}></i>);
        }
    },
    {
        title: 'Role',
        dataIndex: 'role',
        key: 'role',
        render: (text: string) => <Badge inverted className="shadow txt-center">{text}</Badge>,
    },
    {
        title: 'Action',
        key: 'action',
        render: (_: any, record: any) => {
            const approved = record?.user?.apps?.enterpriseApp === 'enabled';
            return (
                <Space size="middle">
                    <Button
                        shadow={!loadingAction && !approved}
                        disabled={loadingAction || approved}
                        onClick={() => accept(record.user)}

                    >
                        <i className="fa fa-check"></i>&nbsp;Accept
                    </Button>
                    <Button
                        bg="red"
                        shadow={!loadingAction}
                        disabled={loadingAction}
                        onClick={() => approved ? remove(record.user) : reject(record.user)}
                    >
                        <i className={`fa fa-${approved ? 'trash' : 'times'}`}></i> &nbsp;{approved ? "Remove" : "Reject"}
                    </Button>
                </Space>
            )
        },
    },
    ]
};

export default columns;