import React, { useEffect } from 'react';
import { Text, Button } from '../../../style/components';
import { DatePicker, Select } from 'antd';
import { connect } from 'react-redux';
import moment, { Moment } from 'moment';
import { previousDay } from '../../../utils/date';

const format = "YYYY-MM-DD h:mm:ss a";
 const FilterBox = ({ setEndTime, interval, setStartTime, garages, backgroundColor = 'white', color } : any)=>{
  function disabledDate(current : Moment | null | any) : boolean {
    return new Date(current?.toString()) > previousDay()
  }
 return  (
    <div
      style={{
      display: "grid", 
      height: "100%", 
      backgroundColor,
      color,
      gridAutoColumns: "auto auto auto auto 1fr", 
      padding: ".5em 1em .5em 1.5em",
      placeItems: "end start"}}
    >
      <Text width="100%" textAlign="center" size={2} bold>Filter</Text>
      <Text textAlign="left" bold>Select Garage</Text>
      
      <Select
        defaultValue={"Garage A"}
        style={{ borderRadius: 10, width: "100%", placeSelf: "end" }}

      >
        {garages && garages[0] && garages[0].map(({ name }: any)=><Select.Option key={name} value={name}>{name}</Select.Option>)}
        </Select>

      <Text color={backgroundColor} textAlign="left" margin=".5em 0 0 0" bold>Start Date</Text>
  
      <DatePicker
      disabledDate={disabledDate}
        style={{ borderRadius: 8}}
        onChange={(e: any) => setStartTime(e)}
        value={moment(interval.startTime, format)}
      />
     <Text  color={backgroundColor} bold margin=".5em 0 0 0">End Date</Text>
      <DatePicker
      disabledDate={disabledDate}
        style={{ borderRadius: 8}}
        onChange={(e: any) => setEndTime(e)}
        value={moment(interval.endTime, format)}
      />
      <Button shadow margin=".8em 0 0 0" style={{placeSelf: "end stretch"}}>Generate Data</Button>
    </div> 
  )}
  const mapStateToProps = (state: any) => {
    const { garages  } = state
    return { garages }
  };


  export default connect(mapStateToProps,{  })(FilterBox);
  