import { actions } from "../reducer/announcement";
import api from "../../api";
import { Notification } from "../../style/components";

type APICall = () => Promise<any>;
export const asyncHandler = async (apiCall: APICall, showError = false) => {
  try {
    let payload = await apiCall();
    if (!payload || !payload.success)
      throw new Error(payload.message || "Error");
    return await payload;
  } catch (e) {
    if (showError) {
      Notification.error({
        title: "Error: Annoucement",
        message: String(e?.message || e),
      });
    }
    console.log(e);
  }
};

export const addAnnouncement = (eid: string, props: any) => {
  return async (dispatch: any) => {
    const payload = await asyncHandler(
      () => api.addAnnoucement(eid, props),
      true
    );
    if (payload && payload.success) {
      dispatch({
        type: actions.ADD_ANNOUNCEMENT_SUCCESS,
        payload: { announcement: props },
      });

      Notification.success({
        title: "Success",
        message: 'Your Announcement was added',
      });
    } else dispatch({ type: actions.ACTION_ANNOUNCEMENT_ERROR });
  };
};

export const editAnnouncement = (eid: string, props: any) => {
  return async (dispatch: any) => {
    dispatch({ type: actions.ACTION_ANNOUNCEMENT_PENDING });
    const payload = await asyncHandler(
      () => api.editAnnoucement(eid, props.id, props),
      true
    );

    if (payload && payload.success) {
      dispatch({
        type: actions.EDIT_ANNOUNCEMENT_SUCCESS,
        payload: {
          announcement: props,
        },
      });

      
      Notification.success({
        title: "Success",
        message: 'Your Announcement was Edited',
      });
    } else dispatch({ type: actions.ACTION_ANNOUNCEMENT_ERROR });
  };
};

export const deleteAnnouncement = (eid: string, props: any) => {
  return async (dispatch: any) => {
    dispatch({ type: actions.ACTION_ANNOUNCEMENT_PENDING });
    const payload = await asyncHandler(
      () => api.deleteAnnoucement(eid, props.id),
      true
    );

    if (payload && payload.success) {
      dispatch({
        type: actions.DELETE_ANNOUNCEMENT_SUCCESS,
        payload: { announcement: props },
      });

      Notification.success({
        title: "Success",
        message: 'Your Announcement was deleted',
      });
    } else dispatch({ type: actions.ACTION_ANNOUNCEMENT_ERROR });
  };
};

export const getAnnouncements = (eid: string) => {
  return async (dispatch: any) => {
    dispatch({ type: actions.GET_ANNOUNCEMENTS_PENDING });
    const payload = await asyncHandler(() => api.getAnnoucements(eid), true);
    if (payload && payload.success) {
      dispatch({
        type: actions.GET_ANNOUNCEMENTS_SUCCESS,
        payload: { announcements: payload?.announcements },
      });
    } else dispatch({ type: actions.GET_ANNOUNCEMENTS_ERROR });
  };
};
