import { useState, useEffect } from "react";
import API from "../api";
import { User } from "../model/domain/user";
import { asyncHandler } from "../utils";

export const useEnterprise = ({ userId }: { userId: string }) => {
  const [state, setter] = useState({
    enterpriseUsers: [],
    enterprise: {
      id: undefined,
    },
    loading: false,
    error: false,
  }) as any;

  const setState = (obj: any) => setter({ ...state, ...obj });

  const getEnterpriseUsers = async (eId: string) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.getEnterpriseUsers(eId),
      notify: false,
    });

    setState({
      loading: false,
      error: res?.success || true,
      enterpriseUsers: res?.enterpriseUsers || [],
    });
  };

  const rejectEnterpriseUser = async (user: User) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.rejectEnterpriseUser(state.enterprise.id, user.id),
      successMessage: "User was Rejected",
      errorMessage: "User was not Rejected",
    });
    setState({ loading: false, error: res?.success || true });
    getEnterpriseUsers(state.enterprise.id)
  };

  const removeEnterpriseUser = async (user: User) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.removeEnterpriseUser(state.enterprise.id, user.id),
      successMessage: "User was Rejected",
      errorMessage: "User was not Rejected",
    });
    setState({ loading: false, error: res?.success || true });
    getEnterpriseUsers(state.enterprise.id)
  };

  const acceptEnterpriseUser = async (user: User) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.acceptEnterpriseUser(state.enterprise.id, user.id),
      successMessage: "User was Accepted",
      errorMessage: "User was not Accepted",
    });
    setState({ loading: false, error: res?.success || true });
    getEnterpriseUsers(state.enterprise.id)
  };


  const getEnterprise = async (userId: string) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.getEnterpriseByUserId(userId),
      errorMessage: "Could not get User Enterprise",
    });
    setState({
      loading: false,
      error: res?.success || false,
      enterprise: res.enterprise || {}
    });

  };

  useEffect(() => {
    if (userId) getEnterprise(userId);
  }, [userId]);


  useEffect(() => {
    if (state.enterprise.id)
      getEnterpriseUsers(state.enterprise.id);
  }, [state.enterprise]);


  return {
    ...state,
    removeEnterpriseUser,
    rejectEnterpriseUser,
    acceptEnterpriseUser,
    getEnterprise,
  };
};

export default {
  useEnterprise,
};
