import React, { useState } from "react";
import { Text, Header, Notification } from "../../../style/components";
import {
  txtDisabledColor as disabled,
  primaryColor as color,
} from "../../../config";
import Login from "./login";
import Sigup from "./signup";
import ForgotPassword from "./forgot-password";
import api from "../../../api";
import { useSelector } from "react-redux";
import { finished } from "stream";

const states = {
  SIGNUP: "SIGNUP",
  LOGIN: "LOGIN",
  PASSWORD_SELECT_EMAIL: "PASSWORD_SELECT_EMAIL",
  PASSWORD_CONFIRM_EMAIL: "PASSWORD_CONFIRM_EMAIL",
  PASSWORD_RESET: "PASSWORD_RESET",
};

const UserSelectBox = () => {
  const delay = 1500;
  const [state, setState] = useState(states.LOGIN);
  const [passwordLoading, setLoading] = useState(false);
  const loading = useSelector((state: any) => state.user.loading);

  const [reset, setResetFields] = useState({
    user_email: "",
    authtoken: "",
  });

  const toggler = () =>
    setState(state === states.LOGIN ? states.SIGNUP : states.LOGIN);

  
  const error = (e : any) => Notification.error({ title: "Error", message: String(e) })
  const notify = (e : string) => ()=>Notification.success({ title: "Success", message: String(e) })
  const finished = () =>  setLoading(false)
  const changeState = (s: string) =>setState(s)

  const selectEmail = (user_email: string) => {
    setLoading(true);
    api
      .forgotPassword({ user_email })
      .then((res: any) => {
        if (res.success) {
          changeState(states.PASSWORD_CONFIRM_EMAIL);
          notify("4 Digit Code was sent to your email")
          setResetFields({ ...reset, user_email });
        } else throw new Error(res.message);
      })
      .catch(error)
      .finally(finished);
  };

  const confirmEmail = (otp: string) => {
    const { user_email } = reset;
    setLoading(true);
    api
      .confirmEmail({ user_email, otp })
      .then((res: any) => {
        if (res.success) {
          setResetFields({ ...reset, authtoken: res.token });
          changeState(states.PASSWORD_RESET);
        } else throw new Error(res.message);
      })
      .catch(error)
      .finally(finished);
  };

  const resetPassword = (user_password: string) => {
    setLoading(true);
    const { authtoken } = reset;
    api
      .resetPassword({ authtoken }, { user_password })
      .then((res: any) => {
        if (res.success) {
          changeState(states.LOGIN);
          notify("Your password was Reset")
        } else throw new Error(res.message);
      })
      .catch(error)
      .finally(finished);
  };

  const passwordForgotToggler = () => setState(states.PASSWORD_SELECT_EMAIL);

  const back = () => setState(states.LOGIN);

  switch (state) {
    case states.PASSWORD_SELECT_EMAIL:
      return (
        <ForgotPassword.SelectEmail
          back={back}
          selectEmail={selectEmail}
          loading={passwordLoading}
        />
      );

    case states.PASSWORD_RESET:
      return (
        <ForgotPassword.Reset
          back={back}
          resetPassword={resetPassword}
          loading={passwordLoading}
        />
      );

    case states.PASSWORD_CONFIRM_EMAIL:
      return (
        <ForgotPassword.ConfirmEmail
          back={back}
          confirmEmail={confirmEmail}
          loading={passwordLoading}
        />
      );

    case states.SIGNUP:
      return (
        <Wrapper state={state} toggler={toggler}>
          <Sigup loading={loading} />
        </Wrapper>
      );

    default:
      return (
        <Wrapper state={state} toggler={toggler}>
          <Login
            passwordForgotToggler={passwordForgotToggler}
            loading={loading}
          />
        </Wrapper>
      );
  }
};

const Wrapper = ({ children, state, toggler }: any) => {
  const isActive = (s: string) => state === s;
  return (
    <div className="noselect" style={{ width: "100%", padding: "1.5em" }}>
      <div style={{ textAlign: "right" }}>
        <span
          className="bold pointer"
          onClick={!isActive(states.LOGIN) ? toggler : () => {}}
          style={{ color: !isActive(states.LOGIN) ? disabled : color }}
        >
          Login
        </span>{" "}
        &nbsp;
        <span
          className="bold pointer"
          onClick={!isActive(states.SIGNUP) ? toggler : () => {}}
          style={{ color: !isActive(states.SIGNUP) ? disabled : color }}
        >
          Signup
        </span>
      </div>

      <Header bold textAlign="left" size={2}>
        {state}
      </Header>
      <Text color={disabled} textAlign="left">
        {state} to Continue to ParkStash Dasboard
      </Text>
      {children}
    </div>
  );
};

export default UserSelectBox;
