import React, { useState, useEffect } from 'react';
import { Grid, Text } from '../../../style/components';
import { Card, Button, Modal } from '../../../style/components';
import { OptionCard } from '../score-card/score-card';
import { Input, Select, DatePicker, Progress, message } from 'antd';
import { primaryColor } from '../../../config';
import bargraph from '../../../assets/bargraph.jpg';
import lineChart from '../../../assets/linechart.jpg';
import multiLineChart from '../../../assets/multiLineChart.jpg';
import { Widget } from '../../../model/domain/dashboard';
import building from '../../../assets/build-chart3.svg';

type ElTypes = 'line-chart' | 'bar-graph';
const GraphAdder = ({ add }: { add: (el: Widget) => void }) => {
    const AdderModal = Modal({
        title: "Create Your Component",
        hideCancel: true,
        message: <MultiStep close={() => AdderModal.close()} add={add} />
    })

    return (<Card bgColor="lightgray" style={{ boxSizing: "border-box" }} width="95%" grid shadow={false} placeSelf="start">
        <Button
            rounded
            minWidth="1.5em"
            minHeight="1.5em"
            onClick={() => AdderModal.open()}>
            <i className="fa fa-plus"></i>
        </Button>
    </Card>)
}


const ChartSelector = ({ setType, type }: { setType: (el: any) => void, type: any }) => {
    const charts = [{
        title: "Line Chart",
        type: "lineChart"
    }]
    return (
        <Grid customCols="auto auto auto">
            <OptionCard
                bg="linear-gradient(-30deg, coral 10%, rgba(190, 103, 120, 0.9) 100%)"
                el={<img style={{ maxHeight: "150px" }} src={lineChart} />}
                onClick={() => setType("lineChart")}
                selected={"lineChart" === type}
                title="Line Chart"
            />
             <OptionCard
                bg="linear-gradient(-30deg, purple 10%, rgba(190, 103, 120, 0.9) 100%)"
                el={<img style={{ maxHeight: "150px" }} src={multiLineChart} />}
                onClick={() => setType("multiLineChart")}
                selected={"multiLineChart" === type}
                title="Multi Line Chart"
            />
            <OptionCard
                bg="linear-gradient(-30deg, #1ecb7d 10%, rgba(30, 203, 180, 0.7) 100%)"
                el={<img style={{ maxHeight: "150px" }} src={bargraph} />}
                onClick={() => setType("stackedGraph")}
                title="Bar Graph"
                selected={"stackedGraph" === type}
            />
        </Grid>
    )
}

const BackgroundSelector = ({ setBackgroundColor, backgroundColor }: { setBackgroundColor: (el: string) => void, backgroundColor: string }) => {
    const colors = [
        { color: primaryColor, name: "green" },
        { color: "#EC7063", name: "red" },
        { color: "#8D6392", name: "purple" },
        { color: "#31AFD4", name: "blue" },
        { color: "#FFFFFF", name: "white" }
    ]
    return (
        <Grid customCols="auto auto auto auto">
            {colors
                .map(({ color, name }: { color: string, name: string }) => <OptionCard
                    key={color}
                    bg={color}
                    style={{ color: color === "#FFFFFF" ? primaryColor : "white" }}
                    onClick={() => setBackgroundColor(color)}
                    selected={color === backgroundColor}
                    title={name.charAt(0).toUpperCase() + name.substring(1)}
                />)}
        </Grid>
    )
}

const NameSelector = ({ title, setTitle }: any) => {

    return (
        <div>
            <div className="txt-shadow">Pick a name for your Graph:</div>
            <Input onChange={(e) => setTitle(e.target.value.trim())} />

            <div className="txt-shadow">Pick the Days of the week</div>
            <Select defaultValue="All Days" disabled >
            </Select>

            <div className="txt-shadow">Pick the Days of the week</div>
            <DatePicker.RangePicker disabled />
        </div>
    )
}

const Confirmation = ({ finish }: any) => {
    const [count, setCount] = useState(0);
    useEffect(() => {
        const interval = setInterval(() => {
            setCount(count => count + 5);
        }, 300);

        return () => {
            clearInterval(interval);
        };
    }, []);

    useEffect(() => {
        if (count >= 100) {
            finish();
            message.success('Your Changes was saved');
        }
    }, [count])
    return (
        <Grid width={"100%"}>
            <div style={{ height: "200px" }}>
                <img src={building} style={{ height: "200px" }} />
            </div>
            <Progress percent={count} style={{ width: 400 }} />
            <Text bold color={"white"}>
                {count < 30 ? "Creating " : "Setting up "}
                your Chart</Text>

        </Grid>
    )
}

const MultiStep = ({ add, close }: any) => {
    const [state, setState] = useState({
        step: 0,
        completed: false
    });

    let defaultProps = {
        "timeSeriesQuery": {
            "metricType": "garage_occupancy_count",
            "crossSeriesReducer": "SUM"
        },
        "backgroundColor": "#33B482",
        "xAxisLabel": "T",
        "yAxisLabel": "Occupancy"
    }

    const [chart, setChart] = useState<Widget | any>({
        title: "",
        type: "",
        selectType: ""
    });
    const setType = (selectedType: string) => {
        const type = selectedType === 'multiLineChart'? 'lineChart' : selectedType;
        setChart({ ...chart, type, selectedType });
        setState({ ...state, completed: true });
    }

    const setTitle = (title: string) => {
        setChart({ ...chart, title });
        setState({ ...state, completed: true });
    }

    const setBackgroundColor = (backgroundColor: string) => {
        if(chart.selectedType === 'multiLineChart'){
            defaultProps = {
                ...defaultProps, timeSeriesQuery : {
                    ...defaultProps.timeSeriesQuery,
                    crossSeriesReducer: 'NONE'
                }
            }
        }

        setChart({ ...chart, [chart.type]: { ...defaultProps, backgroundColor } });
        setState({ ...state, completed: true })
    }

    const forward = () => {
        if (state.step < InputComponents.length) {
            setState({ step: state.step + 1, completed: false });
        }
    }

    const finish = () => {
        add(chart);
        close();
    }

    const backward = () => {
        if (state.step > 0) {
            setState({ ...state, step: state.step - 1 })
        }
    }

    const InputComponents = [
        <ChartSelector 
         setType={setType} 
            type={chart.selectedType} 
        />,
        <BackgroundSelector 
            setBackgroundColor={setBackgroundColor} 
            backgroundColor={chart[chart.type]?.backgroundColor} 
        />,
        <NameSelector 
            title={chart.title} 
            setTitle={setTitle} 
        />,
        <Confirmation finish={finish} />]
    return <>
        <div>
            {InputComponents[state.step]}
        </div>
        <Grid width={"100%"} placeItems="end">
            <Button onClick={forward} shadow disabled={!state.completed}>Next</Button>
        </Grid>
    </>;
}

export default GraphAdder;