import React  from "react";
import { Garage } from "../../model/domain/garage";
import { Card, Header, Text, Grid, Badge, Notification } from "../../style/components";
import { Progress } from "antd";
import { primaryColor } from "../../config";

const GarageCard = ({ name, address, maxOccupancy, active, edit, occupancyUpdateSource }: Garage & any) => {
  const underConstruction = ()=>Notification.warning({ title: "Warning", message: "Garage Occupancy Table Under Construction"})
  return (
    <Card shadow minWidth="40vw">
      <Header textAlign="left">
        <Grid customCols="auto auto 1fr auto" placeSelf="end">
          <Text bold>{name}</Text>
          <Badge 
            style={{fontSize: ".65em"}} 
            disabled={!active}
          >
            {active? "Active": "Disabled"}
          </Badge>
          <div></div>
          <span className="noselect">
            <i className="fa fa-cog pointer hoverr" onClick={underConstruction}></i> &nbsp;
            <i className="fa fa-edit pointer hoverr" onClick={edit}></i> &nbsp;
            <i className="fa fa-trash pointer" style={{color :"gray"}}></i> &nbsp;
          </span>
        </Grid>
      </Header>
      <Text color="gray" textAlign="left">
        {" "}
        &nbsp; {address}
      </Text>
      <Grid customCols="auto 1fr">
        <i className="fa fa-car txt-lgx" aria-hidden="true"></i>
        <Grid placeSelf="stretch" placeItems="center end" gridGap=".5em">
        <Progress
          strokeWidth={10}
          strokeColor={primaryColor}
          percent={Math.ceil(Math.random() * 100)}
          style={{
            maxWidth: "70%",
            placeSelf: "center end",
            width: "300px",
          }}
        />
        <Badge clasName="grid" style={{placeSelf: "end", marginRight: "2em"}}>
         <div> 
           20 <span style={{fontSize: "1.3em"}}> / </span>{maxOccupancy}
         </div>
        </Badge>
      </Grid>
        <Badge inverted>{occupancyUpdateSource === 'integration'? "Automatic": "Manual"}</Badge>
       
      </Grid>
    </Card>
  );
};

export default GarageCard;
