import { Announcement } from "../../model/domain/announcements";
import announcement from "../../pages/announcement";

interface State {
    announcements: Announcement[];
    loading: boolean;
    error: boolean;
}

interface Action {
    type: string;
    payload: {
        announcement?: Announcement;
        announcements?: Announcement[];
        index? : number;
    };
    loadingAction: boolean;
    loadingAnnouncements?: boolean;
    error?: boolean;
}

const intialState = {
    error: false,
    loadingAnnouncements: false,
    loadingAction: false,
    announcements: [] as Announcement[],
};

export const actions = {
    ACTION_ANNOUNCEMENT_PENDING: " ACTION_ANNOUNCEMENT_PENDING", //Includes Edit, Add, Delete
    ACTION_ANNOUNCEMENT_ERROR: "ACTION_ANNOUNCEMENT_ERROR", //Includes Edit, Add, Delete

    EDIT_ANNOUNCEMENT_SUCCESS: "EDIT_ANNOUNCEMENT_SUCCESS",
    ADD_ANNOUNCEMENT_SUCCESS: "ADD_ANNOUNCEMENT_SUCCESS",
    DELETE_ANNOUNCEMENT_SUCCESS: "DELETE_ANNOUNCEMENT_SUCCESS",

    GET_ANNOUNCEMENTS_PENDING: "GET_ANNOUNCEMENTS_PENDING",
    GET_ANNOUNCEMENTS_SUCCESS: "GET_ANNOUNCEMENTS_SUCCESS",
    GET_ANNOUNCEMENTS_ERROR: "GET_ANNOUNCEMENTS_ERROR",
};

export default function rootReducer(state = intialState, action: Action): any {

    const { payload, type } = action;

    switch (type) {
        case actions.ACTION_ANNOUNCEMENT_PENDING:
            return { ...state, loadingAction: true };

        case actions.GET_ANNOUNCEMENTS_PENDING:
            return { ...state, loadingAnnouncements: true };

        case actions.ADD_ANNOUNCEMENT_SUCCESS:
            return {
                ...state,
                loadingAction: false,
                announcements: [...state.announcements, payload.announcement],
            };

        case actions.DELETE_ANNOUNCEMENT_SUCCESS:
            return {
                ...state,
                loadingAction: false,
                announcements: state.announcements.filter(
                    (item: Announcement) => item !== payload.announcement
                ),
            };

        case actions.EDIT_ANNOUNCEMENT_SUCCESS:
            const { id: idToFind } = payload.announcement as Announcement;
            const tempAnnouncement = [ ...state.announcements ];
            const index = state.announcements.reduce((acc, cur, i)=> cur.id === idToFind? i : acc, -1)
            tempAnnouncement[index as number]  = payload.announcement as Announcement
            return {
                ...state,
                loadingAction: false,
                announcements: tempAnnouncement
            };

        case actions.GET_ANNOUNCEMENTS_SUCCESS:
            return {
                ...state,
                loadingAnnouncements: false,
                announcements: payload?.announcements || []
            };

        case actions.GET_ANNOUNCEMENTS_ERROR:
            return {
                ...state,
                error: true,
                loadingAnnouncements: false,
                announcements: [],
            };


        case actions.ACTION_ANNOUNCEMENT_ERROR:
            return {
                ...state,
                error: true,
                loadingAction: false,
                loadingAnnouncements: false
            };

        default:
            return state;
    }
}
