import { useState, useEffect } from "react";
import API from "../api";
import { asyncHandler } from "../utils";
import defaultWidgets from '../json/default-widgets.json';
import { Widget } from "../model/domain/dashboard";

interface State {
    widgets?: any |  Widget [];
    dId?: string;
    enterprise?: {
      id: string
    },
    loading?: boolean,
    error?: boolean,
}

export const useWidgets = ({ userId }: { userId: string }) => {
  const [state, setter] = useState({
    widgets: defaultWidgets as any,
    dId: "",
    enterprise: {
      id: "",
    },
    loading: false,
    error: false,
  });

  const setState = (obj: State) => setter({ ...state, ...obj });

  const getWidgets = async () => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.getWidgets(state.enterprise.id),
      errorMessage: "Could not Load Widgets",
    });
    setState({ loading: false,
         error: res?.success || true,
         dId: res?.dashboards[0]?.id || "", 
        widgets: [...defaultWidgets, ...res?.dashboards[0]?.widgets || []]
    });
  };


  const saveWidgets = async (widgets : Widget[]) => {
     await asyncHandler({
      apiCall: () => API.editWidgets(state.enterprise.id, state.dId, { widgets : widgets.splice(2) }),
      errorMessage: "Could not Edit Widgets",
      successMessage: "Widget was Edited",
    });
  };

  const addWidget = (widget : Widget)=>{
    const widgets =  [...state.widgets, widget];
    setState({ widgets});
    saveWidgets([...widgets]);
  }

  const removeWidget = (index : number)=>{
    const widgets  = state.widgets.filter((_: Widget, i : number ) => i !== index)
    setState({ widgets });
    saveWidgets([...widgets]);
  }


  const getEnterprise = async (userId: string) => {
    setState({ loading: true, error: false });
    const res = await asyncHandler({
      apiCall: () => API.getEnterpriseByUserId(userId),
      errorMessage: "Could not get User Enterprise",
    });
    setState({
      loading: false,
      error: res?.success || false,
      enterprise: res.enterprise || {}
    });
  };


  useEffect(() => {
    if (state.enterprise.id)
      getWidgets();
  }, [state.enterprise.id]);

  return {
    ...state,
    getWidgets,
    getEnterprise,
    removeWidget,
    addWidget,
    saveWidgets
  };
};

export default {
  useWidgets,
};
