import React, { useMemo, useCallback, useEffect } from 'react';
import { AreaClosed, Line, Bar, LinePath } from '@vx/shape';
import { curveMonotoneX } from '@vx/curve';
import { GridRows, } from '@vx/grid';
import { scaleTime, scaleLinear } from '@vx/scale';
import { withTooltip, Tooltip, defaultStyles } from '@vx/tooltip';
import { WithTooltipProvidedProps } from '@vx/tooltip/lib/enhancers/withTooltip';
import { localPoint } from '@vx/event';
import { LinearGradient } from '@vx/gradient';
import { max, extent, bisector } from 'd3-array';
import { AxisLeft } from '@vx/axis';
import { Group } from '@vx/group'
import withFilter from '../filters/with-filter';
import { primaryColor } from '../../../config';


type Data = {
  at: string,
  value: number,
  isMissing: boolean
};

const colors = ["#FFFFFF", "#F7DC6F", "#004FFF", "#F26CA7", "#00F3F3", "#E71D36", "#C5D86D", "#2C634E"]

export const accentColor = "lightgray";
export const accentColorDark = '#75daad';

const getDate = (d: Data) => new Date(d?.at);
const getStockValue = (d: Data) => Number(d?.value);
const bisectDate = bisector<Data, Date>(d => new Date(d?.at)).left;

export type AreaProps = {
  width: number;
  height: number;
  background: string;
  loading : boolean;
  background2?: string;
  dataPoints: [Data[]];
  margin?: { top: number; right: number; bottom: number; left: number };
};

export default withFilter(withTooltip<AreaProps, Data>(({
  width,
  dataPoints = [[ ]],
  height,
  loading = false,
  background,
  background2,
  margin = { top: 0, right: 0, bottom: 0, left: 0 },
  showTooltip,
  hideTooltip,
  tooltipData,
  tooltipTop = 0,
  tooltipLeft = 0, }: AreaProps & WithTooltipProvidedProps<Data>) => {
  const color = background === "#FFFFFF" ? primaryColor : "#FFFFFF";
  if (width < 10) return null;
  const rand = Math.random() * 500
  const tooltipStyles = {
    ...defaultStyles,
    background,
    border: '1px solid white',
    color,
  };

  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;
  const dateScale = scaleTime({
      range: [0, xMax],
      domain: extent(dataPoints[0] || [], getDate) as [Date, Date],
    })

  const stockValueScale = scaleLinear({
      range: [yMax, 0],
      domain: [0, (max(dataPoints[0] || [], getStockValue) || 0) + yMax / 3],
    })

  const reducer = (datum: Data, index: number, data: Data[]): boolean => data[index].value > 0;

  const getAllToolTipData = () => {
    const currentIndex = dataPoints[0].indexOf(tooltipData as any);
    return dataPoints.map((data: Data[], index: number) => ( <div key={index} style={{ padding: 2 }}>
        <span className="round " style={{ background: colors[index] }}></span> &nbsp;
        {`Occupancy: ${getStockValue(tooltipData as any)}`}<br />
      </div>)
      );
  }
  const handleTooltip = useCallback((event: React.TouchEvent<SVGRectElement> | React.MouseEvent<SVGRectElement>) => {
    const { x } = localPoint(event) || { x: 0 };
    const x0 = dateScale.invert(x);
    const index = bisectDate(dataPoints[0]  || [], x0, 1);
    const d0 = dataPoints[0][index - 1];
    const d1 = dataPoints[0][index];
    let d = d0;
    if (d1 && getDate(d1)) {
      d = x0.valueOf() - getDate(d0).valueOf() > getDate(d1).valueOf() - x0.valueOf() ? d1 : d0;
    }
    showTooltip({
      tooltipData: d,
      tooltipLeft: x,
      tooltipTop: stockValueScale(getStockValue(d)),
    });
  },
  [showTooltip, stockValueScale, dateScale],
  );

  return (
    <div style={{ minHeight: "300px" }} className="appear">
      <svg width={width} height={height}>
        <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={`url(#area-background-gradient${rand})`}
          rx={14}
        />
        <LinearGradient id={"area-background-gradient" + rand} from={background} to={background2 || background} />
        <LinearGradient id="area-gradient" from={accentColor} to={accentColor} toOpacity={0.1} />
        <GridRows<number>
          scale={stockValueScale}
          width={xMax}
          strokeDasharray="3,3"
          stroke={accentColor}
          strokeOpacity={0.3}
          pointerEvents="none"
        />
        <Group left={40}>
   
          {dataPoints?.length === 1 ?
            <AreaClosed<Data>
              data={dataPoints[0] || []}
              x={d => dateScale(getDate(d))}
              y={d => stockValueScale(getStockValue(d))}
              yScale={stockValueScale}
              strokeWidth={1}
              defined={reducer}
              stroke="url(#area-gradient)"
              fill="url(#area-gradient)"
              curve={curveMonotoneX}
            /> :
            dataPoints.map((data: any, index: number) => <LinePath<Data>
              data={data}
              key={index}
              defined={reducer}
              x={d => dateScale(getDate(d))}
              y={d => stockValueScale(getStockValue(d))}
              strokeWidth={3}
              stroke={colors[index]}
              curve={curveMonotoneX}
            />)

          }
        </Group>
        <Bar
          x={0}
          y={0}
          width={width}
          height={height}
          fill="transparent"
          rx={14}
          onTouchStart={handleTooltip}
          onTouchMove={handleTooltip}
          onMouseMove={handleTooltip}
          onMouseLeave={() => hideTooltip()}
        />
        {tooltipData && (
          <g>
            <Line
              from={{ x: tooltipLeft, y: 0 }}
              to={{ x: tooltipLeft, y: yMax }}
              stroke={accentColorDark}
              strokeWidth={2}
              pointerEvents="none"
              strokeDasharray="5,"
            />
          </g>
        )}
        <AxisLeft
          left={10}
          scale={stockValueScale}
          stroke={background}
          axisClassName="sup"
          tickStroke={background}
          tickLabelProps={() => ({
            fill: background,
            stroke: color,
            fontSize: 15,
            textAnchor: 'start',
          })}
        />
      </svg>
      {tooltipData && (
        <div>
          <Tooltip
            style={{
              ...tooltipStyles,
              background: color,
              textAlign: "left",
              boxShadow: "0 2px 6px rgba(0,0,0,0.25), 0 3px 3px rgba(0,0,0,0.22)",
              borderRadius: ".5em",
              zIndex: 1000,
              color: background
            }}
            top={tooltipTop - 12}
            left={tooltipLeft + 12}
          > <span style={{ width: "12px", display: "inline-block" }}></span> &nbsp; <b>Garages</b>
            {getAllToolTipData()}
          </Tooltip>
          <Tooltip

            top={yMax - 14}
            left={tooltipLeft}
            style={{
              ...defaultStyles,
              minWidth: 72,
              padding: 2,
              background: color,
              color: background,
              textAlign: 'center',
              transform: 'translateX(-60%)',
            }}
          >
            {getDate(tooltipData).toISOString().substring(5,10)}
          </Tooltip>
        </div>
      )}
    </div>

  );
}));
