import React, { useState, useContext, useEffect, useRef } from 'react'
import { Card, Grid, Button, Modal, Text } from '../../../style/components';
import { ComponentsContext } from '../../../context/components';
import {  OptionCard } from './score-card';
import AddPanel from '../modals/score-card';
import panelJSON from './score-card-json';
import { message } from 'antd';
import DraggableArea from './dragable';
import { Widget } from '../../../model/domain/dashboard';
import withStorage, { Storable } from '../../withStorage';
import { gray } from '../../../config';

const intoHeadings = (str: string): string => {
  if (!intoHeadings || intoHeadings.length === 0) throw new Error("");
  return str.charAt(0).toUpperCase() + str.substring(1).replace(/([a-z](?=[A-Z]))/g, '$1 ')
}

const PanelList = withStorage(({ loadData, saveData }: Storable) => {
  const KEY = 'score-cards';

// const data = JSON.parse(loadData(KEY)) || [] as Widget[];

  const [currentScoreCards, setCurrent] = useState<Widget[]>([]);

  const addPanels = (scoreCard: Widget) => {
    setCurrent([...currentScoreCards, scoreCard]);
    message.success('Your Panel was added');
  }

  const { editMode } = useContext(ComponentsContext);
  const closeAddModal = () => addModal?.close();

  const addModal = Modal({
    title: "Add",
    hideCancel: true,
    message: <AddPanel
      currentScoreCard={currentScoreCards}
      close={closeAddModal}
      setCurrent={setCurrent}
      options={[]}
    />
  });

  const remove = (index: number) => {
    setCurrent(currentScoreCards.filter((item: Widget, i: number) => i != index))
    message.success('Your Panel was deleted');
  }

  useEffect(() => saveData(KEY, JSON.stringify(currentScoreCards)), [currentScoreCards]);

  return (
    <div className="slide-up" style={{background: gray, width: "100%", gridColumn: "1/-1"}}>
      <DraggableArea items={currentScoreCards} setItems={setCurrent} remove={remove} editMode={editMode}>
        {currentScoreCards.length < 5 && <Card bgColor="lightgray" grid shadow={false}>
          <Button rounded minWidth="1.5em" minHeight="1.5em" onClick={() => { addModal.open() }}>
            <i className="fa fa-plus"></i>
          </Button>
        </Card>}
      </DraggableArea>
    </div>);
})

export default PanelList;