import React from 'react';
import { Grid, Text, Button } from '../../style/components';
import * as  Yup from 'yup';
import { useFormik } from 'formik';
import {  Switch, InputNumber, Input } from 'antd';
import { Tooltip } from 'antd';

const validationSchema = Yup.object({
    name: Yup.string().required("Required!").min(5, "Garage must contain at least 6 characters"),
    maxOccupancy: Yup.number().required("Required!")
});
const defaultValue = {name: "", active : false, maxOccupancy: 1 };

const GarageInfoForm = (props: any) => {
    const  loading = props.loading === true?  true : false;
    const { handleSubmit, handleChange, errors, setFieldValue, values, touched, handleBlur } = useFormik({
        initialValues: props.garage || defaultValue,
        onSubmit(values) {
            props.submit(values);
        },
        validationSchema
    })

    const error = values.name < 6;
    return (
        <form
            onSubmit={handleSubmit}
            className="shake"
            style={{ 
            height: "100%",
            display: "grid", 
            background: "white",
            padding: "2em",
            width: "100%", 
            placeItems: "center stretch",
            gridGap: ".4em" }}>

                <div style={{placeSelf: "stretch", display: "grid", placeItems: "stretch"}} >
                    <Text textAlign="left" bold>Enter the Name of your Garage<span className="red">*</span></Text>
                    <Input
                        style={{placeSelf: "end"}}
                        type={'text'}
                        name={'name'}
                        onBlur={handleBlur}
                        suffix={
                          <Tooltip title={"Garage must contain at least 6 characters"}>
                                <i className={`fa fa-${error ? 'times-circle red' : 'check green'}`} style={{ position: "relative" }}></i>
                            </Tooltip>
                        }
                        value={values.name}
                        onChange={handleChange}
                        placeholder={"Example: SJSU Garage 1"} />
                </div>

                <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0">
                    <Text textAlign="left" placeSelf="end start" bold>
                        Activate Garage
                        <span className="red">*</span>
                    </Text>
                    <Switch 
                        checked={values.active}
                        style={{placeSelf: "end"}}
                        onChange={(checked: boolean)=>setFieldValue("active", checked)}
                    />
                </Grid>

                <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0">
                    <Text textAlign="left" placeSelf="end start" bold>
                        Max Garage Occupancy
                        <span className="red">*</span>
                    </Text>
                     <InputNumber 
                        min={1}  
                        style={{placeSelf: "end"}}
                        value={values.maxOccupancy} 
                        onChange={(e)=>setFieldValue("maxOccupancy", e)} 
                        name={"maxOccupancy"} 
                     />
                </Grid>
                {loading ? <Button loading disabled style={{fontSize: ".8em"}}>Loading</Button> :
                <Button.Normal
                    style={{fontSize: ".8em"}}
                    type="submit"                    
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
                </Button.Normal>}
        </form>
    );
};

export default GarageInfoForm;