
import React from 'react';
import styled, { keyframes } from 'styled-components';
import {  Grid,  Avatar} from '../../style/components'
import {  mobileWidth, primaryColor } from '../../config';
import { useLocation } from "react-router";

const slide = keyframes` from {transform: translateX(22em)} to {transform: translateX(0)}`;
const TopNavBar = ({ user, logout}: any) => {
  const location = useLocation().pathname;
  return (
        <MiniNav>
          <Text className="capitalize bold txt-md txt-left" style={{ minWidth: "100px" }}>
            {location === "/" || location === "/garages"  ? "Garage Analytics" : location.replace("/", "") }
          </Text>

          <div className="grid" style={{ gridTemplateColumns: "auto auto auto" }}>
            <div>
             <i style={{fontSize: "2.5em"}} className="fa fa-car"></i>
             <i className="fa fa-plus"></i>
            </div>

            <div>
             <i style={{fontSize: "2em"}} className="fa fa-bullhorn"></i>
             <i className="fa fa-plus"></i>
            </div>
         </div>

         <div className="grid">
              <Avatar url={"https://f0.pngfuel.com/png/136/22/profile-icon-illustration-user-profile-computer-icons-girl-customer-avatar-png-clip-art.png"}/>
         </div>

        </MiniNav>)
}


const Nav = styled.div`
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-size: .8em; 
    display: grid;
    padding: .3em;
    position: fixed;`;

    const Text = styled.div`
    padding-left: 10em;
    @media(max-width: ${mobileWidth}){
       padding-left: 0;
       font-size: 1.2em;
    } 
  `;

const TopNav = styled(Nav)`
    right : 0;
    bottom: 0;
    height: 100vh;
    width: 25em;
    font-size: 15px;
    grid-template-rows: repeat(20, auto);
    background: white;  
    top: 0;
    place-items: center;
    z-index: 9;
    @media (max-width: ${mobileWidth}) {
      height: 70px;
      width: 100vw;
      top : inherit;
      top: 0;
      grid-template-rows: 1fr;}`;

const topNav = `
left: 0;
width: 100vw;
height: 4em; 
color: ${primaryColor};
padding-left: 15em;
grid-gap: 1em;
padding: 0 1.5em;
grid-template-columns: 1fr auto auto;
grid-template-rows: 1fr;`;

const MiniNav = styled(TopNav)`
  width: 5em;
 
    ${topNav}
    place-items: center end;
    @media(max-width: ${mobileWidth}){
      padding-left: 0
      grid-gap: .5em;
    } `;

export default TopNavBar;