import React, { useState } from "react";
import {  Button, Text, Notification } from "../../../style/components";
import img from "../../../assets/enterprise.svg";
import { Input } from "antd";
import { primaryColor } from "../../../config";
import otpImg from '../../../assets/otp.svg';

const ConfirmEmail = ({ back, confirmEmail, loading }: any) => {
  const [otp, setOtp] = useState("");
  return (
    <Wrapper 
    image={otpImg}
    back={back}
     action={() => confirmEmail(otp)} 
     loading={loading}>
    
      <Text textAlign="left" bold>
        Enter your Code
      </Text>
      <Input value={otp} onChange={(e) => setOtp(e.target.value.trim())} />
    </Wrapper>
  );
};

const Reset = ({ back, resetPassword, loading }: any) => {
  const [secret, setSecret] = useState({
    user_password: "",
    confirm_password: "",
  });

  const submit = () => {
    try {
      if (secret.user_password.length < 5) {
        throw new Error("Password Length Should be at least 6 characters");
      }
      if (secret.user_password !== secret.confirm_password) {
        throw new Error("Passwords do not Match");
      }
      resetPassword(secret.user_password);
    } catch (e) {
      Notification.error({ title: "Error", message: e.message });
    }
  };

  return (
    <Wrapper 
    back={back} 
    action={submit} 
    loading={loading}>
  
      <Text textAlign="left" bold>
        Enter your Password
        </Text>
      <Input
        name="password"
        type="password"
        onChange={({ target: { value: user_password } }) =>
          setSecret({ ...secret, user_password })
        }
      />
      <br />

      <Text textAlign="left" bold>
        Confirm your Password
        </Text>
      <Input
        name="confirm-password"
        type="password"
        onChange={({ target: { value: confirm_password } }) =>
          setSecret({ ...secret, confirm_password })
        }
      />
    </Wrapper >
  );
};

const SelectEmail = ({ selectEmail, back, loading }: any) => {
  const [user_email, setEmail] = useState("");

  return (
    <Wrapper
      back={back}
      loading={loading}
      action={() => selectEmail(user_email)}>
  
      <Text textAlign="left" bold>
        Enter Your Email <span className="red">*</span>
        </Text>
      <div style={{ width: "100%" }}>
        <Input
          name="email"
          value={user_email}
          onChange={(e) => setEmail(e.target.value.trim())}
        />
      </div>
    </Wrapper>
  );
};

const Wrapper = ({ children, back, loading, action, image = img }: any) => {
  return (
    <div style={{ padding: "1em 2em" }} className="shake">
      <div
        className="bold pointer txt-left"
        onClick={back}
        style={{ color: primaryColor, width: "100%", padding: 0 }}
      >
       <i className="fa fa-chevron-left"></i> &nbsp;Back
      </div>
      <img
        src={image}
        style={{ height: "50%", maxWidth: "40vw", width: "300px" }}
      />
      <form onSubmit={(e) => e.preventDefault()}>
        {children}
        <Button
          padding={".3em"}
          loading={loading}
          disabled={loading}
          margin=".8rem 0"
          width="100%"
          onClick={action}>
          Confirm
       </Button>
      </form>
    </div>
  );
};

export default {
  SelectEmail,
  Reset,
  ConfirmEmail,
};
