import { useState, useEffect } from "react";
import API from "../api";

export const useEnterprises = (userId = null) => {
  const [state, setState] = useState({
    enterprises: [],
    loading: false,
    error: false
  });

  const getEnterpises = async () => {
    setState({ ...state, loading: true, error: false });
    try {
      const res = await API.getEnterprises();
      setState({ ...state, loading: false, error: false, enterprises: res.enterprises })
    } catch (e) {
      error(e);
    }
  }

  const error = (e: any) => {
    console.log(e)
    setState({ ...state, loading: false, error: true, enterprises: [] })
  }

  useEffect(() => { getEnterpises();}, [userId]);
  return {
    ...state,
    getEnterpises,
    refresh: getEnterpises
  };
};

export default {
  useEnterprises
};
