import React, { useRef, useEffect, useState, useContext } from "react";
import { Card, Header, Grid } from "../../../style/components";
import FilterBox from "./filter";
import { MenuItem } from "../../../style/components/menu";
import { ComponentsContext } from "../../../context/components";
import { primaryColor } from "../../../config";
import { useMetricData } from "../../../hooks/metrics";
import { Widget } from "../../../model/domain/dashboard";
import { TimeSeriesQuery } from "../../../model/domain/metrics";
import { Popover } from 'antd';

interface Editable {
  editable: boolean;
  remove: (i: number) => void;
}
type Type = 'barChart' | 'lineChart'
const withFilter = (WrappedComponent: any) => {
  const HOC = (props: Widget & Editable) => {
    const { editable, remove, title, type } = props as Widget | any;
    const restProps = props[type as 'lineChart'];
    const { backgroundColor } = restProps as any;
    const [el, setter] = useState({ x: 0, y: 0 });
    const container = useRef<any>(null);
    const { editMode } = useContext(ComponentsContext);
    const chart = props.type
    const timeSeries = props[chart]?.timeSeriesQuery;

    const { data, interval, setStartTime, setEndTime } = useMetricData(timeSeries as TimeSeriesQuery);

    const color = backgroundColor === "#FFFFFF" ? primaryColor : "#FFFFFF";

    const resize = () => {
      const { clientWidth: x, clientHeight: y } = container.current;
      setter({ x, y });
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(resize, [container?.current?.clientWidth]);

    useEffect(() => {
      window?.addEventListener("resize", resize);
      return () => window.removeEventListener("resize", resize);
    }, []);


    return (
      <Card
        placeSelf="stretch"
        shadow
        grid
        customCols={"1fr auto"}
        bgColor={backgroundColor}
        gridGap={"0 1em"}
        className="slide-up noselect"
      >
        {
          <Header
            className="grid"
            color={color}
            style={{
              gridColumn: "1/-1",
              width: "100%",
              gridTemplateColumns: "1fr auto auto",
              placeItems: "center start",
            }}
            bold
            size={4}
          >
            <div
              style={{
                fontSize: "1.4em",
                padding: ".35em",
                borderRadius: "1em",
                border: "2px solid",
                textTransform: "capitalize",
                lineHeight: ".8em",
                placeSelf: "start",
              }}
            >
              {title}
            </div>
            {
              <>
                <span className="hide-mobile" style={{ padding: "0 .5em" }}>
                  <MenuItem active inverted>
                    1D
                  </MenuItem>
                  <MenuItem inverted>1W</MenuItem>
                  <MenuItem inverted>1M</MenuItem>
                  <MenuItem inverted>3M</MenuItem>
                  <MenuItem inverted>1Y</MenuItem>
                </span>

                {!editMode && (
                  <Popover
                    placement="bottom"
                    content={<FilterBox
                      interval={interval}
                      backgroudColor={backgroundColor}
                      color={color}
                      setEndTime={setEndTime}
                      setStartTime={setStartTime}
                    />}
                  >
                    <a
                      className="ant-dropdown-link rounded shadow"
                      style={{
                        color: primaryColor,
                        background: "white",
                        fontSize: "1.5em",
                      }}
                      onClick={(e) => e.preventDefault()}
                    >
                      <i className={`fa fa-${data.loading? 'spinner spin' : 'filter'}`}></i>
                    </a>
                  </Popover>)
                }
              </>
            }
            {editMode && (
              <Grid
                gridGap="0"
                placeItems="stretch"
                cols="2"
                className="ant-dropdown-link rounded shadow"
                style={{
                  background: editable ? "white" : "gray",
                  fontSize: "1.35em",
                  border: "2px solid white",
                }}
              >
                <div
                  className={editable ? "hover-txt" : ""}
                  style={{
                    color: !editable ? "white" : "teal",
                    borderRight: `2px solid`,
                    padding: ".1em .5em",
                  }}
                >
                  <i className="fa fa-edit"></i>
                </div>
                <div
                  className={editable ? "hover-txt" : ""}
                  onClick={() => (editable ? remove() : {})}
                  style={{
                    color: !editable ? "white" : "red",
                    padding: ".1em  .5em",
                  }}
                >
                  <i className="far fa-trash-alt"></i>
                </div>
              </Grid>
            )}
          </Header>
        }
        <div
          style={{ minWidth: "30vw", width: "100%", marginTop: "1em" }}
          ref={container}
        >
          {<WrappedComponent
            width={el.x}
            height={300}
            loading={data.loading}
            dataPoints={data.data || [[{at : '2010', value : 0, isMissing: true}]]}
            background={backgroundColor}
          />}
        </div>
      </Card>
    );
  };

  return HOC;
};

export default withFilter;
