import React, { useState } from 'react';
import { Grid, Switch } from '../../style/components';
import '../../style/main.scss'
import 'antd/dist/antd.css';
import Layout from '../../layout';
import Listings from './listings';
import Analytics from './analytics';
import ScoreCards from "./score-card/score-cards";

import useGarages from '../../hooks/garage';
import bg from '../../assets/login-bg.svg';

const activePages = {
  "ANALYTICS": "ANALYTICS",
  "LISTINGS": "LISTINGS"
}
const Garage = () => {
  const { } = useGarages();
  const [activePage, setActivePage] = useState(activePages.LISTINGS)
  return (
    <Layout sidebar background={bg}>
      <Grid
        className="left-on-desktop"
        style={{ minHeight: "100vh" }}
        minWidth="40vw" max="90vw"
        placeItems="start center"
        customRows="auto 1fr"
      >
        <Grid width="100%" customCols="1fr auto 1fr">
          <div></div>
          <Switch
            style={{ placeSelf: "stretch center" }}
            items={Object.keys(activePages)}
            onClick={(active: string) => setActivePage(active)}
            active={activePage}
          />
          <ScoreCards />
        </Grid>
        {
          activePage === activePages.LISTINGS ? <Listings /> : <Analytics />
        }

      </Grid>
    </Layout>
  );
}

export default Garage;
