import React from 'react';
import LineChartComponent from '../line-chart';
import StackedGraph from '../bar-graph/stacked-graph';
import { Widget } from '../../../model/domain/dashboard';

export const getComponents = (El: Widget, remove : any) =>{
    const isEditable = (El : any)=> El.editable !== false;
    
    switch(El.type){
        case "scoreCard":
        return <div key={El.title}>Score Card:  {JSON.stringify(El)}</div>
        
        case "lineChart":   
        return  <LineChartComponent key={El.title} {...El} editable={isEditable(El)} remove={remove} />

        default:
        return<StackedGraph key={El.title} {...El}editable={isEditable(El)} remove={remove} />
    }
}