import { Grid, MenuItem } from "../";
import React from 'react';


const Switch = ({ onClick, active, items, style } : any)=>(
    <Grid padding=".5em" style={style}>
    <div
      className="shadow"
      style={{
        background: "#CDF7E0",
        padding: ".2em",
        borderRadius: "1.5em",
      }}
    >
      {items.map((item: string) => (
        <MenuItem
          key={item}
          style={{ borderRadius: "1.5em" }}
          active={active === item}
          onClick={() => onClick(item)}
        >
          {item.charAt(0).toUpperCase() +
           item.substring(1).toLowerCase()}
        </MenuItem>
      ))}
    </div>
  </Grid>
  );


  export default Switch;