import React, { useEffect } from 'react';
import { Grid  } from '../../style/components';
import { primaryColor } from '../../config';
import car from "../../assets/car.svg";
import useGarages from '../../hooks/garage';
import { Garage } from '../../model/domain/garage';
import GarageInfoForm from './garage-info-form';

export const GarageEditor = ({ close, userId, garage, refresh }: any) => {
    const { editGarage, getEnterprise, loading } = useGarages();
    const submit = async (garage : Garage)=>{
        await editGarage(garage);
        close();
        refresh();
    }
    useEffect(()=>{getEnterprise(userId)}, [])
    return (
        <Grid customCols="4fr 5fr" width="100%" height="100%" gridGap="0">
            <div style={{ background: primaryColor, height: "100%", padding: "1em" }}>
                <img src={car} style={{ maxWidth: "100%" }} />
            </div>
            <GarageInfoForm submit={submit} garage={garage} loading={loading}/>
        </Grid>);
}
