import React, { useState, useEffect } from "react";
import GoogleMapReact from "google-map-react";
import { Input, Tooltip } from "antd";
import { Card, Button } from "../../style/components";
import { usePlaces } from "../../hooks/places";

const AnyReactComponent = ({ showText, className }: any) => (
  <div>
    <i
      className={"fa fa-map-marker txt-lg red " + className}
      aria-hidden="true"
    ></i>
    {showText && (
      <Card width="5em" padding=".4em">
        Click Me to Move
      </Card>
    )}
  </div>
);

const SimpleMap = ({ forward, setGeo }: any) => {
  const origin = { lat: 32, lng: -92 };
  const {
    searchText,
    selectResult,
    error,
    search,
    setSearchText,
    resetSearch,
    getAddress,
  } = usePlaces();

  const [state, setter] = useState({
    location: origin,
    pointerLocation: origin,
    zoom: 15,
    pointerMoved: false,
  });
  const setState = (obj: any) => setter({ ...state, ...obj });

  const start = { gridColumn: 1, gridRowStart: 1 };
const selectResultLocation = (e: any) => {
    e.preventDefault();
    if (search.found) {
      setState({
        location: search.resultLocation,
        pointerLocation: search.resultLocation,
      });
     selectResult(search.result);
    }
    resetSearch();
  }

  //   useEffect(() => {
  //     if (navigator.geolocation) {
  //         navigator.geolocation.getCurrentPosition(function (position) {
  //           const { latitude : lat, longitude : lng } = position.coords;
  //           const cordinates = { lat, lng }
  //             setState({ pointerLocation: cordinates, location: cordinates });
  //         });
  //     }
  // }, []);

  return (
    <div
      style={{
        display: "grid",
        height: "50vh",
        width: "100%",
        gridTemplateColumns: "1fr",
      }}
    >
      <form
        onSubmit={selectResultLocation}
        style={{
          ...start,
          zIndex: 10,
          marginTop: ".3em",
          width: "100%",
          height: "4em",
        }}
      >
        <Input
          suffix={
           <Tooltip title={"Please enter the Full Address"}>
                <i className={`fa fa-${error ? 'times-circle red' : 'check green'}`} style={{ position: "relative" }}></i>
            </Tooltip>
          }
          onChange={({ target }: any) => {
            setSearchText(target.value);
            setState({ pointerMoved: false });
            getAddress(target.value);
          }}
          placeholder="Enter your Garage Address"
          value={searchText}
          style={{ height: "3em", width: "80%", borderRadius: "4em" }}
        />
        {search.result.length > 0 && (
          <Card
            onClick={selectResultLocation}
            className="pointer noselect"
            shadow
            margin="0 .5em"
          >
            {search.result}
          </Card>
        )}
      </form>
      <Button
        shadow={state.pointerMoved && ! error}
        disabled={!state.pointerMoved || error}
        onClick={() => {
          setGeo({...state.pointerLocation, address: searchText});
          forward();
        }}
        style={{
          ...start,
          zIndex: 10,
          justifySelf: "center",
          height: "2.3em",
          position: "relative",
          top: "calc(100% - 4em)",
        }}
      >
        Next &nbsp;
        <i className="fa fa-chevron-right" aria-hidden="true"></i>
      </Button>
      <div style={{ ...start, justifySelf: "stretch" }}>
        <GoogleMapReact
          bootstrapURLKeys={{ key: "AIzaSyB4z2lv6jvxicb7dsQ-K8fFxRcjt07YxQI" }}
          center={state.location}
          defaultZoom={state.zoom}
          onChange={({ zoom, center: location }: any) =>
            setState({ zoom, location })
          }
          onClick={({ _, __, lat, lng }: any) =>
            setState({ pointerLocation: { lat, lng }, pointerMoved: true })
          }
        >
          <AnyReactComponent
            className={!state.pointerMoved ? "grow" : ""}
            lat={state.pointerLocation.lat}
            lng={state.pointerLocation.lng}
            showText={!state.pointerMoved}
          />
        </GoogleMapReact>
      </div>
    </div>
  );
};

export default SimpleMap;
