import React from 'react';
import UserSelect from './user-select';
import userStates from '../../json/user-state.json'
import Enterpise from './enterprise-select/enterprise';
import { useSelector } from 'react-redux';


const UserHandleBox = ()=>{
 const state = useSelector((state: any) => state.user.userState)

   switch(state){    
        case userStates.ENTERPISE_SELECT:
        return <Enterpise.Select/>

        case userStates.ENTERPISE_PENDING:
        return <Enterpise.Pending />
         
        default:
        return <UserSelect />;

    }
}



export default UserHandleBox;